import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CCardFooter,
	CCol,
  CRow,
  CCardHeader
} from '@coreui/react'

import Josss from '../users/Users.js'
import Mantab from '../users/accum.js'

class App extends Component {
  constructor() {
    super()
    this.state = {
      data: []
    }
  }

componentDidMount(){
   const urlFetch = fetch('https://api.kawalcorona.com/indonesia/provinsi')
   urlFetch.then( res => {
      if(res.status === 200)
         return res.json()   
   }).then( resJson => {
      this.setState({
          data: resJson
      })
   })
}
render() {
    const { data } = this.state
		var label = data.map(function(e){
			return parseInt(e.attributes.Provinsi);
});		
console.log(label)
    return (
      <div className="App">
			<CCard>
      <CCardBody>
			<Josss/>
			</CCardBody>	
			</CCard>
			<CCard>
      <CCardBody>
			<Mantab/>
			</CCardBody>	
			</CCard>			
      </div>
    );
  }
}

export default App;