import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CCardFooter,
	CCol,
  CRow,
  CCardHeader
} from '@coreui/react'


class Mantab extends Component {
  constructor() {
    super()
    this.state = {
      data: []
    }
  }

componentDidMount(){
   const urlFetch = fetch('https://indonesia-covid-19.mathdro.id/api/provinsi')
   urlFetch.then( res => {
      if(res.status === 200)
         return res.json()   
   }).then( resJson => {
      this.setState({
          data: resJson.data
      })
   })
}
render() {
    const { data } = this.state
    return (
      <div className="App">		
			<p>
        <header className="App-header">
          <h1 className="App-title">Accumulated Copit Cases per Province</h1>
        </header>
			</p>
			<div>
    <>
      <CRow>
          {
            data.map(obj => {	
              return (
        <CCol xs="12" sm="6" md="4">
							<CCard color="">
          <CCardHeader color="success">
           <h5>{`Provinsi : ${obj.provinsi}`}</h5>
          </CCardHeader>
          <CCardBody>
					<div>{`Positif : ${obj.kasusPosi}`}</div>
					<div>{`Sembuh : ${obj.kasusSemb}`}</div>
					<div>{`Meninggal : ${obj.kasusMeni}`}</div>
          </CCardBody>
        </CCard>
        </CCol>
				              )
            })
          }
      </CRow>
    </>
		</div>
      </div>
    );
  }
}

export default Mantab;